<template>
  <div class="m-list-component">
    <ul>
      <li v-for="(item, index) in listArr" :key="index" :class="index%2 == 1 ? 'bgc':''">
        <a href="#">
          <span class="item-a">
            <span class="num">{{index+1}}</span>
            <span class="title">{{item.title}}</span>
          </span>
          <span class="item-b">
            <i :class="item.state | typeFilters"></i>
          </span>
        </a>
      </li>
    </ul>
  </div>
</template>

<script>
const stateType = [
  { type: 1, className: "fa fa-level-up up" },
  { type: 2, className: "fa fa-level-down down" },
  { type: 3, className: "middle" }
];
export default {
  props: {
    listArr: Array
  },
  data() {
    return {};
  },
  filters: {
    typeFilters(state) {
      const type = stateType.find(obj => {
        return obj.type == state;
      });
      return type ? type.className : null;
    }
  },

  components: {},

  methods: {}
};
</script>

<style scoped>
.m-list-component {
  background-color: #ffffff;
}
.bgc {
  background-color: #f4f4f4;
}

.m-list-component li {
  width: 10rem;
  height: 1.2rem;
  line-height: 1.2rem;
}
.m-list-component li a {
  color: #000;
}

.item-a .num {
  display: inline-block;
  width: 1.066667rem;
  height: 1.2rem;
  text-align: center;
  color: #aaa;
  font-size: 0.426667rem;
  font-style: italic;
  font-family: PingFangSC-Light;
}
.item-a .title {
  display: inline-block;
  width: 7.866667rem;
  height: 1.2rem;
  font-size: 0.373333rem;
}
.item-b {
  display: inline-block;
  width: 1.066667rem;
  height: 1.2rem;
  text-align: center;
  position: relative;
}

/* 趋势图标 */
.down {
  color: limegreen;
  width: 0.213333rem;
}
.up {
  color: red;
  width: 0.213333rem;
}
.middle {
  height: 1.2rem;
  width: 0.213333rem;
  /* 因为这里没内容 所以随便占个位置 */
  margin-left: 0.266667rem;
}
.middle::before {
  content: "";
  height: 0.08rem;
  width: 0.16rem;
  background-color: maroon;
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
}
</style>