<template>
  <div class="ul-div">
    <ul class="ul-list">
      <li style="fontSize: 16px">
        <span class="titleName">片名</span>
        <span></span>
        <span class="actorName">主演</span>
        <span class="trend">走势</span>
      </li>
      <li v-for="(item, index) in listArr" :key="index" :class="index%2 !=1 ? 'bgc':''">
        <i class="indexIcon">{{index+1}}</i>
        <span class="titleName">{{item.title}}</span>
        <span class="actorName">{{item.actor}}</span>
        <span class="trend">
          <i :class="item.state | typeFilters"></i>
        </span>
      </li>
    </ul>
  </div>
</template>

<script>
const stateType = [
  { type: 1, className: "fa fa-level-up up" },
  { type: 2, className: "fa fa-level-down down" },
  { type: 3, className: "middle" }
];
export default {
  props: {
    listArr: Array
  },
  filters: {
    typeFilters(state) {
      const type = stateType.find(obj => {
        return obj.type == state;
      });
      return type ? type.className : null;
    }
  },

  data() {
    return {};
  },
  created() {
    console.log(this.listArr);
  },

  components: {},

  methods: {}
};
</script>

<style scoped>
/* ul-div */
.ul-div {
  background-color: #ffffff;
  padding: 20px;
}

.bgc {
  background: #f4f4f4;
}

/* ul-list */
.ul-list li {
  display: grid;
  grid-template-columns: 42px 438px 320px 100px;
  height: 60px;
  line-height: 60px;
}
.ul-list li span:nth-of-type(2) {
  overflow: hidden;
  white-space: nowrap;
}
.ul-list li div {
  height: 100%;
}
.indexIcon {
  width: 42px;
  height: 60px;
  color: #aaa;
  font-size: 20px;
  display: inline-block;
  text-align: center;
}

.down {
  color: limegreen;
  height: 100%;
  width: 8px;
  margin-left: 10px;
}
.up {
  color: red;
  height: 100%;
  width: 8px;
  margin-left: 10px;
}
.middle {
  height: 100%;
  width: 8px;
  position: relative;
  margin-left: 10px;
}
.middle::before {
  content: "";
  /* border-left: 1px solid black;
   border-right: 1px solid black; */
  height: 3px;
  width: 8px;
  background-color: maroon;
  display: block;
  position: absolute;
  top: 50%;
  left: 0px;
  transform: translateY(-50%);
}
</style>