import Vue from 'vue'
import VueRouter from 'vue-router'
import Layout from '../components/Layout.vue'

// 主视图
import Home from '../views/home.vue'
import Dramas from '../views/dramas.vue'
import Movie from '../views/movie.vue'
import Variety from '../views/variety.vue'
import Video from '../views/video.vue'
import Message from '../views/message.vue'
import List from '../views/list.vue'

// 追剧
import Hot from '../views/zhuiju/hot.vue'
import Today from '../views/zhuiju/today.vue'
import Tomorrow from '../views/zhuiju/tomorrow.vue'

// 排行榜
import TotalList from '../views/list/totalList.vue'
import DramasList from '../views/list/dramasList.vue'
import VarietyList from '../views/list/varietyList.vue'
import MovieList from '../views/list/movieList.vue'
Vue.use(VueRouter)

  const routes = [
  {
    path: '/',
    name: 'Layout',
    component: Layout,
    redirect: '/home',
    children:[
      {
        path:'/home',
        component: Home,
        meta:{title:'首页'},
        redirect: '/home/today',
        children:[
          {
            path:'/home/today',
            component:Today
          },
          {
            path:'/home/tomorrow',
            component:Tomorrow
          },
          {
            path:'/home/hot',
            component:Hot
          },
        ]
      }
    ]
  },
  {
    path: '/dramas',
    component: Layout,
    children: [
      {
        path:'/',
        component: Dramas,
      }
    ]
  },
  {
    path: '/movie',
    component: Layout,
    children: [
      {
        path:'/',
        component: Movie,
      }
    ]
  },
  {
    path: '/variety',
    component: Layout,
    children: [
      {
        path:'/',
        component: Variety,
      }
    ]
  },
  {
    path: '/video',
    component: Layout,
    children: [
      {
        path:'/',
        component: Video,
      }
    ]
  },
  {
    path: '/message',
    component: Layout,
    children: [
      {
        path:'/',
        component: Message,
      }
    ]
  },
  {
    path: '/list',
    component: Layout,
    children: [
      {
        path:'/',
        component: List,
        redirect: '/list/total',
        children:[
          {
            path:'/list/total',
            name:'total',
            component:TotalList
          },
          {
            path:'/list/dramas',
            component:DramasList
          },
          {
            path:'/list/variety',
            component:VarietyList
          },
          {
            path:'/list/movie',
            component:MovieList
          },
        ]
      }
    ]
  },
]

const router = new VueRouter({
  routes
})

export default router
