<template>
  <div class="layou">
    <div class="PCvue" v-if="isPC">
      <TVHeader class="header"></TVHeader>
      <TVMain :isPC="isPC"></TVMain>
      <!-- footer -->
      <Footer class="footer"></Footer>
    </div>

    <div class="Mvue" v-else>
      <mHeader class="m-header"></mHeader>
      <mMain class="m-Main" :isPC="isPC"></mMain>
      <div class="m-footer">
        <span>©2016-2018 韩剧tv</span>
      </div>
    </div>
  </div>
</template>

<script>
// PC端
import TVHeader from "../components/TVHeader";
import TVMain from "../components/TVMain";
import Footer from "../components/TVFooter";

// 移动端
import mHeader from "./mobileVue/mHeader";
import mMain from "./mobileVue/mMain";

export default {
  // 总布局
  data() {
    return {
      screenWidth: document.body.clientWidth,
      isPC: true
    };
  },

  created() {
     
  },

  mounted() {
    this.isPCandMobile();
      window.onresize = () => {
      this.isPCandMobile();
    };
  },

  components: {
    TVHeader,
    TVMain,
    Footer,
    mHeader,
    mMain
  },

  methods: {
    isPCandMobile(){
      var bodyWidth = document.body.clientWidth;
      if (bodyWidth <= 768) {
        this.isPC = false;
        console.log(bodyWidth);
        console.log("这个是移动端");
        document.documentElement.style.fontSize = bodyWidth / 10 + "px";
      } else {
        this.isPC = true;
      }
    },
  }
};
</script>

<style scoped>
.layou {
  position: relative;
}

.header {
  width: 100%;
  position: fixed;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  z-index: 999;
}

.m-header {
  width: 10rem;
  height: 2.4rem;
  background-color: #ffffff;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9999;
}

.m-Main {
  margin-top: 2.4rem;
}
.m-footer {
  width: 10rem;
  height: 1.093333rem;
  padding-top: 0.133333rem;
  text-align: center;
  color: #aaa;
  line-height: 1.093333rem;
  font-size: 0.373333rem;
  margin: 5px 0;
}
</style>
