<template>
  <div class="mSwiper">
    <swiper
      ref=".                                                               "
      :options="swiperOptions"
    >
      <swiper-slide v-for="(item, index) in imgSwiper" :key="index">
        <img :src="item.imgStr" alt />
        <div class="swiper-title">
          <p>{{item.title}}</p>
        </div>
      </swiper-slide>
      <div class="swiper-pagination" slot="pagination"></div>
      <!-- <div class="swiper-button-prev button-color" slot="button-prev"></div>
      <div class="swiper-button-next button-color" slot="button-next"></div>-->
    </swiper>
  </div>
</template>

<script>
export default {
  props: {
    imgSwiper: Array
  },
  data() {
    return {
      
      swiperOptions: {
        centeredSlides: true,
        spaceBetween: -20,
        notNextTick: true,
        loop: true,
        // 初始索引
        initalSlide: 0,
        // 自动播放
        autoplay: true,
        // 当用户操作是否禁止 autoplay
        disableOnInteraction: false, //用户操作swiper之后，是否禁止autoplay。
        // 切换效果
        effect: "effect",
        speed: 800,
        direction: "horizontal",
        // 左右点击
        navigation: {
          nextEl: ".swiper-button-next",
          prevEl: ".swiper-button-prev"
        },
        // 分页器
        pagination: {
          el: ".swiper-pagination",
          clickable: true
        }
        // swiperSlides: [1, 2, 3]
      }
    };
  },

  components: {},
  computed: {
    swiper() {
      return this.$refs.mySwiper.$swiper;
    }
  },

  methods: {}
};
</script>

<style scoped>
.mSwiper {
  padding-top: 0.133333rem;
  width: 10rem;
  height: 5.653333rem;
  background-color: #fff;
  text-align: center;
}
img {
  width: 9.333333rem;
  height: 4.32rem;
  background-color: #fff;
  border-radius: 0.133333rem;
}
.swiper-title p {
  padding-top: 0.133333rem;
  font-size: 0.4rem;
}
</style>